@font-face {
  font-family: "Barlow";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Barlowmedium.woff2") format("woff2"), url("../fonts/Barlowregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Barlow";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Barlowmedium.woff2") format("woff2"), url("../fonts/Barlowmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Barlow";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Barlowsemibold.woff2") format("woff2"), url("../fonts/Barlowsemibold.woff") format("woff");
  font-display: swap;
}
h2,
h3 {
  margin: 0;
}

p {
  margin: 0;
}

img {
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  border: none;
  padding: 0;
  margin: 0;
  background: none;
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.link:hover {
  color: #999fad;
}
.link:active {
  color: #c7722e;
}

.page {
  height: 100%;
  font-family: "Barlow", "Arial", "Helvetica", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #050215;
  background-color: #ffffff;
}

.page__body {
  margin: 0 auto;
  position: relative;
}

.page-body {
  display: flex;
  flex-direction: column;
  width: 1920px;
  max-width: 1920px;
  height: 1080px;
  background: url("../img/background-image.svg") no-repeat 1110px 188px;
}
.page-body::after {
  content: "";
  position: absolute;
  top: 0;
  left: 293px;
  height: 100%;
  border: 2px solid #e5e5e5;
}

.page-body__page-main {
  margin: 0 106px 0 101px;
}

.page-body__page-footer {
  position: relative;
  bottom: 21px;
  margin: 0 106px 0 101px;
}

.page-header {
  position: absolute;
  top: 64px;
  display: flex;
  align-items: center;
  z-index: 1;
  width: 100%;
}

.page-header__logo {
  cursor: default;
  margin-right: 914px;
  margin-left: 101px;
}

.nav {
  display: flex;
  align-items: center;
  gap: 10px 110px;
  flex-wrap: wrap;
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 59px;
}

.nav__item--current {
  color: #b4977b;
}

.nav__basket {
  display: flex;
}

.nav__basket-sup-icon {
  position: relative;
  top: -5px;
  left: -2px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #000000;
}

.nav__basket-quantity {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  margin: auto;
  width: 3px;
  height: 7px;
  font-weight: 700;
  font-size: 6px;
  line-height: 7px;
  color: #ffffff;
}

.page-main {
  flex-grow: 1;
  display: grid;
  grid-template-columns: 92px 1fr;
  column-gap: 264px;
  min-height: 100%;
}

.page-main__pagination {
  margin-top: 360px;
}

.services {
  position: relative;
}

.services__item {
  position: absolute;
  display: none;
}
.services__item--current {
  display: grid;
  grid-template-columns: max-content 1fr;
  column-gap: 133px;
}

.services__picture-wrapper {
  position: relative;
  grid-row: span 2;
}

.services__image-title {
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  box-sizing: border-box;
  font-weight: 500;
  font-size: 28px;
  line-height: 34px;
  color: #ffffff;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
  padding: 42px 109px;
}

.services__text-wrapper {
  position: relative;
  margin-top: 246px;
}

.services__title {
  max-width: 517px;
  font-size: 64px;
  font-weight: 600;
  line-height: 77px;
  margin-bottom: 40px;
}

.services__description {
  max-width: 642px;
  max-height: 290px;
  line-height: 32px;
  color: rgba(5, 2, 21, 0.75);
  padding-right: 25px;
  margin-bottom: 40px;
  overflow-y: auto;
}

.services__description::-webkit-scrollbar {
  width: 4px;
}

.services__description::-webkit-scrollbar-track {
  background: #e5e5e5;
}

.services__description::-webkit-scrollbar-thumb {
  background-color: #b4977b;
  border-radius: 4px;
  height: 10px;
}

.services__number {
  position: absolute;
  top: 777px;
  right: 5px;
  font-size: 200px;
  line-height: 240px;
  font-weight: 600;
  color: #ffffff;
  -webkit-text-stroke: 1px #b4977b;
  z-index: -1;
}

.services__button {
  display: flex;
  max-width: 178px;
  font-weight: 500;
  font-size: 18px;
  background: #b4977b;
  border-radius: 50px;
  color: #ffffff;
  padding: 19px 32px;
  box-sizing: border-box;
}
.services__button:hover {
  background-color: #999fad;
}
.services__button:active {
  background-color: #c7722e;
  color: #ffffff;
}

.pagination {
  display: flex;
  flex-direction: column;
  row-gap: 182px;
  align-items: center;
}

.pagination__pages {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 15px;
}

.pagination__number {
  font-weight: 500;
  font-size: 54px;
  line-height: 65px;
  color: #e4d6c9;
}
.pagination__number--current {
  color: #b4977b;
}

.pagination__separator {
  position: relative;
  display: block;
  width: 4px;
  height: 200px;
  background: #e5e5e5;
  border-radius: 2px;
}

.pagination__scroll {
  position: absolute;
  top: 0;
  display: block;
  width: 4px;
  height: 42px;
  background: #b4977b;
  border-radius: 2px;
}

.control__button {
  width: 58px;
  height: 79px;
  border: 2px solid #b4977b;
  border-radius: 50px;
}
.control__button:hover {
  border: 2px solid #999fad;
  background-color: #999fad;
}
.control__button:active {
  background-color: #c7722e;
  border: 2px solid #c7722e;
}

.control__button:hover .control__icon {
  fill: #ffffff;
}

.page-footer__social-list {
  position: relative;
  top: -100px;
  margin-left: 419px;
}

.social-list {
  display: flex;
  flex-wrap: wrap;
  max-width: 386px;
  column-gap: 94px;
}

.social-list__link {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
}